.slide {
    width: 100%;
    height: 100%;
    position: relative;

    .swiper-pagination-bullet-active {
        background-color: #000;
    }

    .slide_title {}

    .swiper {
        width: 100%;
        padding-top: 50px;
        padding-bottom: 50px;

        @media screen and (max-width:700px) {
            padding-top: 0px;
            padding-bottom: 0px;
        }
    }

    .swiper-slide {
        background-position: center;
        background-size: cover;
        width: 700px;
        height: 400px;
        text-align: center;

        @media screen and (max-width:700px) {
            width: 100%;
            height: 100%;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
        }
    }
}