.home_box {
    width: 100%;
    height: 92.2vh;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    h2 {
        font-size: 3rem;
        font-weight: bold;

        @media screen and (max-width:800px) {
            font-size: 2rem;

        }
    }

    .line {
        height: .25rem;
        width: 300px;
        background: #000;
        margin: 1rem;
    }

    .job {
        font-size: 1rem;
    }

    .project_btn {
        margin-top: 1rem;
        padding: 1rem;
        background: #000;
        border-radius: 5px;

        a {
            text-transform: uppercase;
            color: #fff;
            text-decoration: none;
            font-size: 1rem;

            @media screen and (max-width:800px) {
                font-size: .7rem;

            }
        }
    }
}