.footer{
    height: 50px;
    width: 100%;
    background: #fff;
    padding:0 100px;
    box-sizing: border-box;
    @media screen and (max-width:700px) {
        padding:0 10px;
      }
    .footer_box{
        font-size: 1rem;
        display: flex;
        width: 100%;
        justify-content: space-between;
        @media screen and (max-width:600px) {
                    font-size: .5rem;
                }
        .sns{
            a{
                img{
                    width: 30px;
                    height: 30px;
                    padding: 0 1rem;
                    @media screen and (max-width:800px) {
                    width: 20px;
                    height: 20px;
                   padding: 0 .5rem;
                }
                }
            }
        }
    }
}